.community {
    width: 100vw;
    height: 100vh;
    /* background-image: url(../../img/3_bg.jpg);
    background-size: cover;
    background-repeat: no-repeat; */

}

.community_wrapper {
    width: 100vw;
    height: 100vh;
    /* background-color: rgba(0, 0, 0, 20%); */
}


.container {
    display: flex;
    margin: 0 auto;
    justify-content: center;
    align-items: center;
    max-width: 1200px;
    height: 100%;
}

.community_content {
    display: flex;
    flex-direction: column;
align-items: center;   
 flex-wrap: wrap;
    gap: 85px;
}
.community_content a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 218.6px;
height: 61.7px;
background: #FED494;
box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.25);
border-radius: 50px;
font-family: 'Kadwa';
font-style: normal;
font-weight: 400;
font-size: 25px;
line-height: 50px;
color: #FFFFFF;
text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
cursor: pointer;
transition: all 0.3s linear;
}
.community_content a:hover{
    background: #FFFFFF;
    color: #4F4F4F;
}

.left_block_community{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 529px;
    height: 492px;
    background: rgba(217, 217, 217, 0.44);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 70px;
    margin-bottom: 90px;
    position: relative;
}
.left_block_community button {
    position: absolute;
width: 100%;
height: 70px;
top: -6%;
left: 0%;
background: #FED494;
box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
border-radius: 25px;
font-family: 'Luckiest Guy';
font-style: normal;
font-weight: 400;
font-size: 32px;
line-height: 32px;
color: #FFFFFF;
text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
border: none;
}


.left_block_content_community {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.left_block_content_community ul li {
    text-align: start;
    font-family: 'Kadwa';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 35px;
    color: #FFFFFF;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.left_block_content_community a{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 187px;
    height: 63px;    
    background: #FFFFFF;
    box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.25);
    border-radius: 50px;
border-radius: 50px;
text-decoration: none;
margin-top: 15px;
transition: all 0.3s linear;
color: #4F4F4F;
}

.left_block_content_community a:hover{
    background: #FED494;
    color: #4F4F4F;
}

